import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(a,s): any {
    if(s)
    {
    	return a.filter(item=>{
    		return Object.values(item).join("").includes(s)
    	})
    }
    else
    {
    return a
    }
  }

}
