import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {

  transform(a,col,toggle): any {
    if(col)
    {	
    	if(col=="tags"||col=="title"||col=="description"||col=="tags"||col=="image")
    	{
    		return a.sort((arr1,arr2)=>{
    			if(toggle)
    			{
    				if(arr1[col]<arr2[col]){return 1}
    				else{return -1}
    				return 0

    			}
    			else
    			{
    				if(arr2[col]<arr1[col]){return 1}
    				else{return -1}
    				return 0
    				
    			}
    		})
    	}
    	else
    	{
    		return a.sort((arr1,arr2)=>{
    			return toggle?(arr1[col]-arr2[col]):(arr2[col]-arr1[col])
    		})
    	}
    }
    else
    {
    	return a
    }
  }

}
