import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})


export class AppComponent {
  products:any[]
  tags:any[]
  s:string|number=""
  order:boolean=false
  col1:string=""
  val1:string=""
  constructor(private http:HttpClient)
  {
  	this.http.get(`http://localhost/api/ecommerce/products.php`).subscribe((d:any[])=>{
      this.products=d
    })

    this.http.get(`http://localhost/api/ecommerce/tags.php`).subscribe((d:any[])=>{
      this.tags=d
    })


  }

  _tags(str)
  {
    this.val1=str
  } 
  _sort(str)
  {
    this.col1=str
    this.order=!this.order
  }
}



export interface products
{
  id:number
  title:string
  description:string
  old:number
  new:number
  rating:number
  discount:number
  tags:string
  category:string
  colors:string
  image:string
}
export interface tag
{
  c:number
  t:string
}
